@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');

/* Navbar */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Inter', sans-serif;
}

.navbar {
  background-color: #21222A;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
  padding: 20px;
}

.navbar-item {
  display: flex;
  align-items: center;
}

.heading {
  color: #61DAFB;
  font-weight: 700;
  font-size: 27px;
  padding: 10px;

}

.heading-1 {
  color: #DEEBF8;
  font-size: 15px
}

.Logo {
  width: 33px;
}

/* Main */
body {
  background-color: #282D35;
}

.main {
  padding: 65px 40px;
  background-image: url('./img/reactjs-icon\ 2.svg');
  background-repeat: no-repeat;
  background-position: 100% 40%;
  color: #FFFFFF;
}

.heading-3 {
  font-weight: 700;
  font-size: 40px;
}

.list-elements {
  padding-left: 40px;
  padding-top: 50px;
  font-weight: 400;
  font-size: 20px;
}

.list-elements li {
  padding: 15px;
}

.list-elements li::marker {
  color: #61DAFB;
  font-size: 1.5em;

}